/*  +-------------------------------------------+
    |              Estilos Globais              |
    +-------------------------------------------+*/

/*cores
cor principal: rgb(122, 144, 46)
cor verde escura: rgb(54, 70, 39)
cor marrom fundo carrossell 
cor fontes rgb(162, 189, 45);*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: sans-serif;
  background: radial-gradient(
    circle at 25%,
    rgb(122, 144, 46),
    rgb(54, 70, 39)
  );
  background-repeat: repeat;
  min-height: 100vh;
}

main {
  flex: 1 1 0;
}

header {
  background-color: rgb(54, 70, 39);
  color: rgb(162, 189, 45);
  display: flex;
  justify-content: space-around;
  margin-bottom: 40px;
  align-items: center;
  padding: 1rem;
}

ul {
  display: flex;
  gap: 40px;
  list-style: none;
}

.nav-link {
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-decoration: none;
  position: relative;
}

.nav-link:hover {
  color: #fcf96c;
}

.nav-link::after {
  content: "";
  transition: 0.25s ease-in-out;
  position: absolute;
  width: 0%;
  height: 2px;
  background-color: #fff;
  bottom: -4px;
  left: 0;
}

.nav-link:hover::after {
  position: absolute;
  width: 100%;
  background-color: #fcf96c;
}

